@tailwind base;
@tailwind components;
@tailwind utilities;

ul {
  counter-reset: item;
}
ul li {
  counter-increment: item;
}
li a::before {
  content: "0" counter(item) ".";
  color: #64ffda;
  margin-right: 5px;
}

section div div ul li{
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 20px;
}

section div div ul li::before{
    content: "â–¹";
    position: absolute;
    left: 0px;
    color: #64ffda;
    font-size: 14px;
    line-height: 12px;
}
main {
  counter-reset: section;
}
main section {
  counter-increment: section;
}
section h2::before {
  content: "0" counter(section) ".";
  color: #64ffda;
  margin-right: 5px;
  font-size: clamp(16px, 3vw, 20px);
}

*::selection {
  background-color: #64ffdb24;
  color: #64ffda;
  user-select: all;
}
body {
  font-family: sans-serif;
}

html {
  scroll-behavior: smooth;
  -webkit-tap-highlight-color: transparent;
}

p{
    margin-bottom: 15px;
}